<template>
  <div class="card mb-4">
    <div class="card-header d-flex justify-content-between pb-0">
      <h6>Councils Table</h6>
      <div class="actions d-flex justify-content-center align-items-center">
        <router-link :to="{ name: 'RegisterCouncil' }"
          ><vsud-badge
            style="margin-left: 15px"
            color="info"
            role="button"
            variant="gradient"
            size="sm"
            >Add</vsud-badge
          ></router-link
        >
      </div>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0" v-if="council_table">
        <table class="table align-items-center mb-0">
          <thead>
            <tr>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Index
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Name
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Phone Number
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Members
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Companies
              </th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(council, index) in council_table"
              :key="council.id"
              role="button"
              class="row-hover"
            >
              <td style="width: 10px; text-align: center">
                <p class="text-xs font-weight-bold mb-0">
                  {{ index + 1 }}
                </p>
              </td>
              <td>
                <div class="d-flex px-2 py-1">
                  <div>
                    <vsud-avatar
                      :img="council.logo.thumbnail_image"
                      size="sm"
                      border-radius="lg"
                      class="me-3"
                      alt="user1"
                    />
                  </div>
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm">{{ council.council_name }}</h6>
                    <p class="text-xs text-secondary mb-0">
                      {{ council.email }}
                    </p>
                  </div>
                </div>
              </td>
              <td>
                <p class="text-xs font-weight-bold mb-0">
                  {{ council.mobile_number }}
                </p>
              </td>
              <td>
                <p class="text-xs font-weight-bold mb-0">
                  {{ council.members_count }}
                </p>
              </td>
              <td>
                <p class="text-xs font-weight-bold mb-0">
                  {{ council.companies_count }}
                </p>
              </td>
              <td class="align-middle" v-if="userType == 'admin'">
                <router-link
                  :to="{
                    name: 'editCouncil',
                    query: { id: council.id },
                  }"
                  class="text-secondary font-weight-bold text-xs"
                  data-toggle="tooltip"
                  data-original-title="Edit user"
                  >Edit</router-link
                >
                <a
                  @click="del_council(council.id)"
                  style="margin-left: 15px"
                  class="text-danger font-weight-bold text-xs"
                  data-toggle="tooltip"
                  data-original-title="Delete user"
                  >Delete</a
                >
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="d-flex justify-content-center align-items-center my-5" v-else>
        <div
          class="orbit-spinner d-flex justify-content-center align-items-center"
        >
          <div class="orbit"></div>
          <div class="orbit"></div>
          <div class="orbit"></div>
        </div>
      </div>
      <div
        class="d-flex justify-content-center align-items-center my-5"
        v-if="report_data"
      >
        <h5 v-if="report_data.length == 0" class="text-dark">No Councils</h5>
      </div>
    </div>
  </div>
</template>

<script>
import VsudAvatar from "@/components/VsudAvatar.vue";
import VsudBadge from "@/components/VsudBadge.vue";
import img1 from "../../assets/img/team-2.jpg";
import { mapState } from "vuex";
import instance from "../../store/axiosConfig.js";
import { useToast } from "vue-toastification";
const toast = useToast();
export default {
  name: "council table",
  data() {
    return {
      council_table: null,
      img1,
    };
  },
  components: {
    VsudAvatar,
    VsudBadge,
  },
  computed: {
    ...mapState({
      userType: (state) => state.auth.user_type,
    }),
  },
  methods: {
    get_council() {
      instance
        .get("/council/")
        .then((res) => {
          console.log(res.data);
          this.council_table = res.data.data;
        })
        .catch((err) => {
          console.log(err.message);
        });
    },
    del_council(id) {
      this.$swal({
        title: "Do you want to Delete this?",
        showCancelButton: true,
        confirmButtonText: "Delete",
      }).then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          instance
            .delete("/council/" + id)
            .then((res) => {
              console.log(res.data);
              toast.warning("Deleted!");
              this.get_council();
            })
            .catch((err) => {
              console.log(err.message);
            });
        }
      });
    },
    getDetails(type, name, id) {
      this.$router.push({
        path: "/reports/" + type + "/" + name + "/" + id,
        params: { sale_id: id },
      });
    },
  },
  created() {
    this.get_council();
  },
};
</script>
