<template>
  <div class="card mb-4">
    <div class="card-header d-flex justify-content-between pb-0">
      <h6>Sales Table</h6>
      <div class="actions d-flex justify-content-center align-items-center">
        <!-- <vsud-select class="filterSales"></vsud-select> -->
        <router-link :to="{ name: 'Reports' }">
          <vsud-badge
            v-if="userType == 'admin'"
            @click="showReport = true"
            color="warning"
            role="button"
            variant="gradient"
            size="sm"
            >Report</vsud-badge
          >
        </router-link>

        <router-link :to="{ name: 'AddSalesMember' }" v-if="userType == 'admin'"
          ><vsud-badge
            style="margin-left: 15px"
            color="info"
            role="button"
            variant="gradient"
            size="sm"
            >Add</vsud-badge
          ></router-link
        >
      </div>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0" v-if="sales_table">
        <table class="table align-items-center mb-0">
          <thead>
            <tr>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Index
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Name
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Phone Number
              </th>
              <!-- <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
              >
                Area
              </th> -->
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Status
              </th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Report
              </th>
              <th class="text-secondary opacity-7"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(member, index) in sales_table" :key="member.id">
              <td style="width: 10px; text-align: center">
                <p class="text-xs font-weight-bold mb-0">
                  {{ index + 1 }}
                </p>
              </td>
              <td>
                <div class="d-flex px-2 py-1">
                  <div>
                    <vsud-avatar
                      :img="member.profile.thumbnail_image"
                      size="sm"
                      border-radius="lg"
                      class="me-3"
                      alt="user1"
                    />
                  </div>
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm">{{ member.name }}</h6>
                    <p class="text-xs text-secondary mb-0">
                      {{ member.email }}
                    </p>
                  </div>
                </div>
              </td>
              <td>
                <p class="text-xs font-weight-bold mb-0">
                  {{ member.phone_number }}
                </p>
              </td>
              <td class="align-middle text-center text-sm">
                <vsud-badge color="success" variant="gradient" size="sm"
                  >Online</vsud-badge
                >
              </td>
              <td class="align-middle text-center">
                <router-link :to="{ name: 'Reports' }">
                  <vsud-badge
                    color="secondary"
                    role="button"
                    variant="gradient"
                    size="sm"
                    >report</vsud-badge
                  ></router-link
                >
              </td>
              <td class="align-middle" v-if="userType == 'admin'">
                <router-link
                  :to="{ path: '/edit-sale/' + member.id }"
                  class="text-secondary font-weight-bold text-xs"
                  data-toggle="tooltip"
                  data-original-title="Edit user"
                  >Edit</router-link
                >
                <a
                  role="button"
                  style="margin-left: 15px"
                  @click="delete_sale(member.id)"
                  class="text-danger font-weight-bold text-xs"
                  data-toggle="tooltip"
                  data-original-title="Delete user"
                  >Delete</a
                >
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="d-flex justify-content-center align-items-center my-5" v-else>
        <div
          class="orbit-spinner d-flex justify-content-center align-items-center"
        >
          <div class="orbit"></div>
          <div class="orbit"></div>
          <div class="orbit"></div>
        </div>
      </div>
      <div
        class="d-flex justify-content-center align-items-center my-5"
        v-if="report_data"
      >
        <h5 v-if="report_data.length == 0" class="text-dark">No Sale</h5>
      </div>
    </div>
  </div>
</template>

<script>
import VsudAvatar from "@/components/VsudAvatar.vue";
import VsudBadge from "@/components/VsudBadge.vue";
// import VsudSelect from "@/components/VsudSelect.vue";
import instance from "../../store/axiosConfig.js";
import { useToast } from "vue-toastification";
const toast = useToast();
import img1 from "../../assets/img/team-2.jpg";
import { mapState } from "vuex";
export default {
  name: "sales table",
  data() {
    return {
      showReport: false,
      sales_table: null,
      img1,
    };
  },
  components: {
    VsudAvatar,
    VsudBadge,
    // VsudSelect,
  },
  computed: {
    ...mapState({
      userType: (state) => state.auth.user_type,
    }),
  },
  methods: {
    salesTable() {
      try {
        this.$store.dispatch("GETSALES").then((res) => {
          console.log(res.data);
          this.sales_table = res.data.data;
        });
      } catch (err) {
        console.log(err.message);
      }
    },
    delete_sale(id) {
      this.$swal({
        title: "Do you want to Delete this?",
        showCancelButton: true,
        confirmButtonText: "Delete",
      }).then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          instance
            .delete("/sale/" + id)
            .then((res) => {
              this.salesTable();
              this.$router.push("/sales");
              toast.warning("Deleted!");
              console.log(res.data);
            })
            .catch((err) => {
              console.log(err.message);
            });
        }
      });
    },
  },
  created() {
    this.salesTable();
  },
};
</script>
<style>
.filterSales {
  margin-bottom: 0 !important;
  margin-right: 10px;
}
.filterSales .form-control {
