<template>
  <div>
    <div class="row">
      <div class="col-md-12 mb-md-0 mb-4">
        <div
          class="card card-body p-0 m-2 border card-plain border-radius-lg d-flex align-items-center flex-row"
        >
          <input
            type="text"
            name=""
            placeholder="Discount"
            class="pay-input"
            v-model="discount_percentage"
          />
        </div>
      </div>
      <div class="col-md-12">
        <div
          class="card card-body p-0 m-2 border card-plain border-radius-lg d-flex align-items-center flex-row"
        >
          <input
            type="text"
            name=""
            placeholder="Discount Amount"
            class="pay-input"
            v-model="discount_amount"
          />
        </div>
      </div>
      <div class="col-md-12">
        <div
          class="card card-body p-0 m-2 border card-plain border-radius-lg d-flex align-items-center flex-row"
        >
          <input
            type="text"
            name=""
            placeholder="Amount"
            class="pay-input"
            v-model="amount"
          />
        </div>
      </div>
      <button type="button" class="form-control" @click="Pay()">Pay</button>
    </div>
  </div>
</template>
<script>
import instance from "../../store/axiosConfig.js";
export default {
  name: "cash",
  data() {
    return {
      discount_percentage: null,
      discount_amount: null,
      amount: null,
    };
  },
  methods: {
    Pay() {
      instance
        .post("/company/pay/cash", {
          discount_percentage: this.discount_percentage,
          discount_amount: this.discount_amount,
          amount: this.amount,
        })
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log(err.message);
        });
    },
  },
};
</script>
<style></style>
