                  type="tel"
                  required=""
                  placeholder="Mobile"
                  aria-label="Phone"
                />
              </div>
              <div class="mb-3 col-md-4 col-sm-12">
                <input
                  class="form-control"
                  v-model="landline_number"
                  id="name"
                  name="name"
                  type="tel"
                  required=""
                  placeholder="Landline"
                  aria-label="Name"
                />
              </div>
              <div class="mb-3 col-md-4 col-sm-12">
                <input
                  class="form-control"
                  v-model="trade_license_number"
                  id="name"
                  name="name"
                  type="text"
                  required=""
                  placeholder="License"
                  aria-label="Name"
                />
              </div>
            </div>
            <div class="mb-3">
              <v-select
                @search="get_activities($event)"
                v-model="selected_activities"
                class="dropdown"
                :options="options.activities"
                :reduce="(option) => option.id"
                label="title"
                placeholder="Activities"
                taggable
                push-tags
              >
              </v-select>
            </div>
            <div class="row">
              <div class="mb-3 col-md-6 col-sm-12">
                <v-select
                  class="dropdown"
                  v-model="legal_status"
                  :options="options.legal_status"
                  placeholder="'Legal Status'"
                  taggable
                  push-tags
                >
                </v-select>
              </div>
              <div class="mb-3 col-md-6 col-sm-12">
                <v-select
                  class="dropdown"
                  v-model="issued_by"
                  :options="options.issued_by"
                  placeholder="'Issued By'"
                  taggable
                  push-tags
                >
                </v-select>
              </div>
            </div>
            <div class="row">
              <div class="mb-3 col-md-5 col-sm-12">
                <v-select
                  @option:selected="get_city"
                  class="dropdown"
                  v-model="country"
                  :options="options.countries"
                  :reduce="(country) => country.id"
                  :placeholder="country_status"
                  label="name"
                  taggable
                  push-tags
                >
                </v-select>
              </div>
              <div class="mb-3 col-md-4 col-sm-12">
                <v-select
                  class="dropdown"
                  @option:selected="get_area"
                  v-model="city"
                  :options="options.cities"
                  label="name"
                  :reduce="(city) => city.id"
                  :placeholder="city_status"
                  taggable
                  push-tags
                >
                </v-select>
              </div>
              <div class="mb-3 col-md-3 col-sm-12">
                <v-select
