                  :reduce="(area) => area.id"
                  :placeholder="area_status"
                  taggable
                  push-tags
                >
                </v-select>
              </div>
            </div>
          </div>
          <div class="text-center">
            <h6>Company Owners</h6>
          </div>
          <div class="card-body">
            <div class="row">
              <div class="mb-3 col-md-6 col-sm-12">
                <input
                  class="form-control"
                  v-model="person_in_charge_name"
                  id="name"
                  name="name"
                  type="text"
                  required=""
                  placeholder="Name"
                  aria-label="Name"
                />
              </div>
              <div class="mb-3 col-md-6 col-sm-12">
                <input
                  class="form-control"
                  v-model="person_in_charge_designation"
                  id="email"
                  name="designation"
                  type="text"
                  required=""
                  placeholder="Designation"
                  aria-label="Designation"
                />
              </div>
            </div>
            <div class="mb-3">
              <input
                class="form-control"
                v-model="person_in_charge_email"
                id="email"
                name="email"
                type="email"
                required=""
                placeholder="Email"
                aria-label="email"
              />
            </div>
            <div class="row">
              <div class="mb-3 col-md-6 col-sm-12">
                <input
                  class="form-control"
                  v-model="person_in_charge_mobile"
                  id="name"
                  name="Mobile"
                  type="tel"
                  required=""
                  placeholder="Mobile"
                  aria-label="Name"
                />
              </div>
              <div class="mb-3 col-md-6 col-sm-12">
                <v-select
                  class="dropdown"
                  v-model="nationality"
                  :options="options.countries"
                  :reduce="(country) => country.id"
                  placeholder="Nationality"
                  label="name"
                  taggable
                  push-tags
                >
                </v-select>
              </div>
            </div>
            <div class="text-center">
              <vsud-button
                @click="set_editable($route.query.company_id)"
                color="dark"
                fullWidth
                variant="gradient"
                class="my-4 mb-2"
                >Save</vsud-button
              >
            </div>
