<template>
  <div>
    <div v-if="directory_companies">
      <CompanyFollowUp
        :title="title"
        v-for="(item, index) in directory_companies"
        :key="index"
        :companyFollowUp="item ? item : 'Loading'"
      />
    </div>
    <div class="d-flex justify-content-center align-items-center mt-64" v-else>
      <div
        class="orbit-spinner d-flex justify-content-center align-items-center"
      >
        <div class="orbit"></div>
        <div class="orbit"></div>
        <div class="orbit"></div>
      </div>
    </div>
  </div>
</template>
<script>
import CompanyFollowUp from "../components/DirectoryFollowUp.vue";
import instance from "../../store/axiosConfig.js";
export default {
  name: "PackageDirectory",
  data() {
    return {
      title: "Directory Package",
      directory_companies: null,
    };
  },
  components: {
    CompanyFollowUp,
  },
  methods: {
    getDetails(id) {
      instance
        .get("/followup/" + id)
        .then((res) => {
          console.log(res.data);
          this.directory_companies = res.data.data;
        })
        .catch((err) => {
          console.log(err.message);
        });
    },
  },
  created() {
    this.getDetails(3);
  },
};
</script>
