<template>
  <div>
    <div class="row">
      <div class="col-md-12 mb-md-0 mb-4">
        <div
          class="card card-body p-0 m-2 border card-plain border-radius-lg d-flex align-items-center flex-row"
        >
          <input
            type="text"
            name=""
            placeholder="Discount"
            class="pay-input"
            v-model="discount_percentage"
          />
        </div>
      </div>
      <div class="col-md-12">
        <div
          class="card card-body p-0 m-2 border card-plain border-radius-lg d-flex align-items-center flex-row"
        >
          <input
            type="text"
            name=""
            placeholder="Discount Amount"
            class="pay-input"
            v-model="discount_amount"
          />
        </div>
      </div>
      <div class="col-md-12">
        <div
          class="card card-body p-0 m-2 border card-plain border-radius-lg d-flex align-items-center flex-row"
        >
          <input
            type="text"
            name=""
            placeholder="Amount"
            class="pay-input"
            v-model="amount"
          />
        </div>
      </div>
      <div class="col-md-12">
        <div
          class="card card-body p-0 m-2 border card-plain border-radius-lg d-flex align-items-center flex-row"
        >
          <input
            type="text"
            name=""
            placeholder="Cheque Number"
            class="pay-input"
            v-model="cheque_number"
          />
        </div>
      </div>
      <div class="col-md-12">
        <div
          class="card card-body p-0 m-2 border card-plain border-radius-lg d-flex align-items-center flex-row"
        >
          <input
            type="text"
            name=""
            placeholder="Bank Name"
            class="pay-input"
            v-model="bank_name"
          />
        </div>
      </div>
      <div class="col-md-12">
        <div
          class="card card-body p-0 m-2 border card-plain border-radius-lg d-flex align-items-center"
        >
          <UploadImg @src="getimg($event)" />
        </div>
      </div>
      <button type="button" class="form-control" @click="Pay()">Pay</button>
    </div>
  </div>
</template>
<script>
import instance from "../../store/axiosConfig.js";
import UploadImg from "../upload/UploadImg";
export default {
  name: "cheque",
  data() {
    return {
      cheque_image: "",
      discount_percentage: null,
      discount_amount: null,
      amount: null,
      cheque_number: null,
      bank_name: null,
      currentImage: null,
    };
  },
  components: {
    UploadImg,
  },
  methods: {
    getimg(value) {
      this.cheque_image = value;
    },
    Pay() {
      let formData = new FormData();
      formData.append("discount_percentage", this.discount_percentage);
      formData.append("discount_amount", this.discount_amount);
      formData.append("amount", this.amount);
      formData.append("cheque_number", this.cheque_number);
      formData.append("bank_name", this.bank_name);
      formData.append("cheque_image", this.cheque_image);
      instance
        .post("/company/pay/cheque", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log(err.message);
        });
    },
  },
};
</script>
